 guarantee that certain\n * high-priority elements (modals, dropdowns, tooltips) will always show up\n * above the other elements in our application. No weird stacking context bugs\n * no z-index arms race.\n *\n * !You should tweak the selector to match your framework.\n * We want to select the top-level element that your application is rendered\n * within. For example, create-react-app uses a <div id=\"root\">, so the correct\n * selector is #root.\n */\n/**\n * #root,\n * #__next {\n *  isolation: isolate;\n * }\n */\n\n/**\n * This is to stop animations for people that explicitly notified their system\n * that they prefer reduced motion. \n */\n@media screen and (prefers-reduced-motion: reduce) {\n  * {\n    /* With very short durations, relying on events JavaScript still works */\n    transition-duration: 0.001ms !important;\n    animation-duration: 0.001ms !important;\n    animation-iteration-count: 1 !important;\n  }\n}\n\n/**\n * Ideally, use `prefers-contrast`, `inverted-colors`, `prefers-color-scheme`\n * media features as well in your projects\n */\n","/*------------------------------------*\\\n  #FONTS\n\\*------------------------------------*/\n\n/* This file contains all @font-face declarations. */\n\n@font-face {\n  font-family: 'Bona Nova';\n  src:\n    local('Bona Nova'),\n    url('@assets/fonts/bonanova-regular.woff2') format('woff2'),\n    url('@assets/fonts/bonanova-regular.woff') format('woff');\n  font-weight: 400;\n  font-style: normal;\n}\n\n@font-face {\n  font-family: 'Bona Nova';\n  src:\n    local('Bona Nova'),\n    url('@assets/fonts/bonanova-bold.woff2') format('woff2'),\n    url('@assets/fonts/bonanova-bold.woff') format('woff');\n  font-weight: 700;\n  font-style: normal;\n}\n\n@font-face {\n  font-family: 'Noto Sans';\n  src:\n    local('Noto Sans'),\n    url('@assets/fonts/notosans-regular.woff2') format('woff2'),\n    url('@assets/fonts/notosans-regular.woff') format('woff');\n  font-weight: 400;\n  font-style: normal;\n}\n","%flex-col {\n  display: flex;\n  flex-direction: column;\n}\n\n%secondary-title {\n  font-family: var(--title-font);\n  font-size: var(--h2-size);\n  font-weight: 400;\n}\n\n%tertiary-title {\n  font-family: var(--title-font);\n  font-size: var(--h3-size);\n  font-weight: 400;\n}\n","/*------------------------------------*\\\n  #ABOUT\n\\*------------------------------------*/\n\n@use 'Abstracts/placeholders';\n\n.about {\n  @extend %flex-col;\n\n  &__title {\n    @extend %secondary-title;\n\n    text-align: center;\n  }\n\n  &__text {\n    color: var(--text-color-main);\n    font-family: var(--text-font);\n    font-size: 1.3rem;\n  }\n\n  &__social-links {\n    align-self: center;\n    margin-top: 1rem;\n  }\n}\n","/*------------------------------------*\\\n  #SOCIAL-LINKS\n\\*------------------------------------*/\n\n.social-links {\n  display: flex;\n  gap: 1.75rem;\n  padding: 0;\n  list-style-type: none;\n\n  &__icon {\n    aspect-ratio: 1 / 1;\n    width: 3rem;\n    min-width: 48px;\n    height: auto;\n  }\n}\n","/*------------------------------------*\\\n  #PROJECT-CARD\n\\*------------------------------------*/\n\n@use 'Abstracts/placeholders';\n\n.project-card {\n  box-shadow: 2px 5px 10px hsla(0, 0%, 0%, 0.2);\n\n  &__screenshot {\n    height: calc(7.5 * var(--h2-size));\n\n    &--light-blue-violet {\n      background-color: hsl(245, 45%, 63%);\n    }\n\n    &--orange {\n      background-color: hsl(32, 67%, 54%);\n    }\n\n    &--green {\n      background-color: hsl(105, 30%, 43%);\n    }\n\n    &--pink {\n      background-color: hsl(335, 45%, 63%);\n    }\n\n    &--light-blue {\n      background-color: hsl(209, 66%, 56%);\n    }\n  }\n\n  &__screenshot-inner {\n    display: flex;\n    flex-shrink: 0;\n    align-items: center;\n    justify-content: center;\n    padding: calc(2 * var(--h2-size)) var(--h2-size);\n    width: 100%;\n    height: 100%;\n  }\n\n  &__screenshot-text {\n    text-align: center;\n    color: hsl(0, 0%, 100%);\n    font-family: var(--title-font);\n    font-size: var(--h2-size);\n  }\n\n  &__info {\n    --info-height: calc(7 * var(--h3-size));\n\n    display: grid;\n    grid-template-areas:\n      'name        links'\n      'description description';\n    grid-template-rows:\n      calc((2 / 7) * var(--info-height))\n      calc((5 / 7) * var(--info-height));\n    grid-template-columns: 1fr auto;\n    padding-inline: 0.75rem;\n    height: var(--info-height);\n  }\n\n  &__name {\n    @extend %tertiary-title;\n\n    grid-area: name;\n    align-self: center;\n    justify-self: start;\n  }\n\n  &__project-links {\n    grid-area: links;\n    align-self: center;\n    justify-self: end;\n  }\n\n  &__description {\n    grid-area: description;\n    color: var(--text-color-main);\n    font-family: var(--text-font);\n  }\n}\n","/*------------------------------------*\\\n  #PROJECT-LINKS\n\\*------------------------------------*/\n\n.project-links {\n  display: flex;\n  gap: 1rem;\n  padding: 0;\n  list-style-type: none;\n\n  &__icon {\n    aspect-ratio: 1 / 1;\n    width: 2rem;\n    min-width: 32px;\n    height: auto;\n  }\n}\n","/*------------------------------------*\\\n  #PAGE\n\\*------------------------------------*/\n\n/**\n * This file contains all global styles applied to the entire page.\n */\n\n.page {\n  display: flex;\n  flex-direction: column;\n  gap: 5rem;\n}\n","/*------------------------------------*\\\n  #HEADER\n\\*------------------------------------*/\n\n/**\n * This file contains all styles related to the header of the site/application.\n */\n\n@use 'Abstracts/functions' as fn;\n@use 'Abstracts/placeholders';\n@use 'sass:math';\n\n.header {\n  /**\n   * These calculations are done to make the gradient boundary always lie in\n   * roughly the same place relative to the photo.\n   */\n  --init-photo-width: 420;\n  --init-photo-height: 595;\n  --photo-aspect-ratio: calc(\n    var(--init-photo-width) / var(--init-photo-height)\n  );\n  --photo-width: calc(100vw - 2 * var(--inline-padding-small));\n  --photo-height: calc(var(--photo-width) / var(--photo-aspect-ratio));\n  --upper-padding: 1.5rem;\n  --gradient-boundary: calc(\n    0.83 * (var(--photo-height) + var(--upper-padding))\n  );\n\n  @extend %flex-col;\n\n  gap: 2rem;\n  background-image: linear-gradient(\n    150deg,\n    var(--secondary-bgc) var(--gradient-boundary),\n    var(--main-bgc) var(--gradient-boundary)\n  );\n  padding: var(--upper-padding) var(--inline-padding-small) 0;\n\n  &__inner {\n    position: relative;\n  }\n\n  &__photo {\n    box-shadow: 0 0 5px 2px hsla(0, 0%, 0%, 0.1);\n    height: 100%;\n  }\n\n  &__title {\n    position: absolute;\n    right: 0;\n    bottom: 0;\n    left: 0;\n    padding-inline: fn.rem(7);\n    text-shadow: 0 fn.rem(2) fn.rem(2) hsla(0, 0%, 0%, 0.3);\n    line-height: 1.1;\n    color: hsl(0, 0%, 100%);\n    font-family: var(--title-font);\n    font-size: 3rem;\n    font-weight: 400;\n  }\n}\n","/*------------------------------------*\\\n  #PORTFOLIO\n\\*------------------------------------*/\n\n@use 'Abstracts/placeholders';\n\n.portfolio {\n  @extend %flex-col;\n\n  padding: var(--inline-padding-small);\n\n  &__title {\n    @extend %secondary-title;\n\n    align-self: center;\n  }\n\n  &__content {\n    @extend %flex-col;\n\n    gap: 2rem;\n  }\n}\n","/*------------------------------------*\\\n  #VARIABLES\n\\*------------------------------------*/\n\n/* This file contains all application-wide variables. */\n\n:root {\n  --main-bgc: hsl(0, 0%, 100%);\n  --secondary-bgc: hsl(192, 100%, 40%);\n  --h2-size: 2.5rem;\n  --h3-size: 1.75rem;\n  --text-color-main: hsl(0, 1%, 35%);\n  --title-font: 'Bona Nova', serif;\n  --text-font: 'Noto Sans', sans-serif;\n  --inline-padding-small: 1.25rem;\n}\n"],"sourceRoot":""}]);
